import { NextRequest, NextResponse } from 'next/server';
import { requireSession } from '@/lib/auth';
import { getAllTutorials, getTutorialsByCategory } from '@/lib/redis/tutorials';
import { SEED_TUTORIALS } from '@/lib/tutorials/seed';

export async function GET(request: NextRequest) {
  try {
    const { user } = await requireSession();
    const category = request.nextUrl.searchParams.get('category');

    let tutorials;
    if (category) {
      tutorials = await getTutorialsByCategory(category);
    } else {
      tutorials = await getAllTutorials();
    }

    // If no tutorials found in Redis, return seed data (for development/testing)
    if (!tutorials || tutorials.length === 0) {
      if (category && category !== 'all') {
        tutorials = SEED_TUTORIALS.filter(t => t.category === category);
      } else {
        tutorials = SEED_TUTORIALS;
      }
    }

    return NextResponse.json({ tutorials });
  } catch (error) {
    console.error('Get tutorials error:', error);
    return NextResponse.json({ error: 'Internal error' }, { status: 500 });
  }
}
