/**
 * Tutorial Redis Operations
 *
 * Handles storage and retrieval of:
 * - Interactive tutorials (tours, tasks, sandboxes)
 * - Tutorial progress tracking
 * - User completion state
 */

import { redis } from './client';
import type { InteractiveTutorial, TutorialProgress } from '@/types/training';

// ============ TUTORIAL OPERATIONS ============

export async function getTutorial(tutorialId: string): Promise<InteractiveTutorial | null> {
  const data = await redis.hgetall(`tutorial:${tutorialId}`);
  if (!data || !('id' in data)) return null;

  const tutorial: any = { ...data };
  if (typeof tutorial.title === 'string') {
    tutorial.title = JSON.parse(tutorial.title);
  }
  if (typeof tutorial.description === 'string') {
    tutorial.description = JSON.parse(tutorial.description);
  }
  if (typeof tutorial.steps === 'string') {
    tutorial.steps = JSON.parse(tutorial.steps);
  }
  if (tutorial.video && typeof tutorial.video === 'string') {
    tutorial.video = JSON.parse(tutorial.video);
  }

  return tutorial as InteractiveTutorial;
}

export async function createTutorial(tutorial: InteractiveTutorial): Promise<void> {
  const data: Record<string, any> = { ...tutorial };
  data.title = JSON.stringify(tutorial.title);
  data.description = JSON.stringify(tutorial.description);
  data.steps = JSON.stringify(tutorial.steps);
  if (tutorial.video) {
    data.video = JSON.stringify(tutorial.video);
  }

  const pipeline = redis.pipeline();
  pipeline.hset(`tutorial:${tutorial.id}`, data);
  pipeline.zadd('tutorials:all', {
    score: new Date(tutorial.createdAt).getTime(),
    member: tutorial.id,
  });
  pipeline.sadd(`tutorials:category:${tutorial.category}`, tutorial.id);
  if (tutorial.publishedAt) {
    pipeline.sadd('tutorials:published', tutorial.id);
  }
  await pipeline.exec();
}

export async function getAllTutorials(): Promise<InteractiveTutorial[]> {
  const tutorialIds = await redis.zrange<string[]>('tutorials:all', 0, -1, { rev: true });
  if (!tutorialIds.length) return [];

  const tutorials = await Promise.all(tutorialIds.map(id => getTutorial(id)));
  return tutorials.filter((t): t is InteractiveTutorial => t !== null);
}

export async function getTutorialsByCategory(
  category: string
): Promise<InteractiveTutorial[]> {
  const tutorialIds = await redis.smembers<string[]>(`tutorials:category:${category}`);
  if (!tutorialIds.length) return [];

  const tutorials = await Promise.all(tutorialIds.map(id => getTutorial(id)));
  return tutorials.filter((t): t is InteractiveTutorial => t !== null);
}

export async function getPublishedTutorials(): Promise<InteractiveTutorial[]> {
  const tutorialIds = await redis.smembers<string[]>('tutorials:published');
  if (!tutorialIds.length) return [];

  const tutorials = await Promise.all(tutorialIds.map(id => getTutorial(id)));
  return tutorials.filter((t): t is InteractiveTutorial => t !== null);
}

// ============ TUTORIAL PROGRESS OPERATIONS ============

export async function getTutorialProgress(
  userId: string,
  tutorialId: string
): Promise<TutorialProgress | null> {
  const data = await redis.hgetall(`tutorial_progress:${userId}:${tutorialId}`);
  if (!data || !('userId' in data)) return null;

  const progress: any = { ...data };
  if (typeof progress.completedSteps === 'string') {
    progress.completedSteps = JSON.parse(progress.completedSteps || '[]');
  }

  return progress as TutorialProgress;
}

export async function updateTutorialProgress(
  userId: string,
  tutorialId: string,
  progress: Partial<TutorialProgress>
): Promise<void> {
  const existing = await getTutorialProgress(userId, tutorialId);

  const updated: TutorialProgress = {
    userId,
    tutorialId,
    currentStep: progress.currentStep ?? existing?.currentStep ?? 0,
    completedSteps: progress.completedSteps ?? existing?.completedSteps ?? [],
    status: progress.status ?? existing?.status ?? 'in_progress',
    startedAt: existing?.startedAt ?? new Date().toISOString(),
    completedAt: progress.completedAt,
    timeSpent: (existing?.timeSpent ?? 0) + (progress.timeSpent ?? 0),
  };

  const data: Record<string, any> = { ...updated };
  data.completedSteps = JSON.stringify(updated.completedSteps);

  await redis.hset(`tutorial_progress:${userId}:${tutorialId}`, data);

  // Add to indices
  await redis.sadd(`user:${userId}:tutorials`, tutorialId);
  if (updated.status === 'completed') {
    await redis.sadd(`user:${userId}:completed_tutorials`, tutorialId);
  }
}

export async function getUserTutorialProgress(userId: string): Promise<TutorialProgress[]> {
  const tutorialIds = await redis.smembers<string[]>(`user:${userId}:tutorials`);
  if (!tutorialIds.length) return [];

  const progresses = await Promise.all(
    tutorialIds.map(tId => getTutorialProgress(userId, tId))
  );
  return progresses.filter((p): p is TutorialProgress => p !== null);
}

export async function getUserCompletedTutorials(userId: string): Promise<string[]> {
  return redis.smembers<string[]>(`user:${userId}:completed_tutorials`);
}
